import React from 'react';
import { Container, Form, Spinner, Button } from 'react-bootstrap';
import PersonOutlineOutlinedIcon from '@mui/icons-material/PersonOutlineOutlined';
import TopProcessBar from 'screen/selectCollege/TopProcessBar';
import { useAppDispatch, useAppSelector } from 'app/hooks';
import { useFormik } from 'formik';
import { useHistory } from 'react-router';
import { BoolToString, formatBoolean } from 'components/helper/helper';
import { getCitizenship, updateCitizenship } from 'app/basicInformationSlice/basicInformationSlice';
import { routers } from 'router/router';
import Cookies from 'js-cookie';
import General from './General';
import CaliforniaResidency from './CaliforniaResidency';
import BirthPalace from './BirthPalace';
import EnrolimentStatus from './EnrolimentStatus';
import CBO from './CBO';
import Counselor from './Counselor';
import FeeWaiver from './FeeWaiver';

interface Props {}

const Citizenship = (props: Props) => {
  const [CBOData, setCBOData] = React.useState<any>(undefined);
  const [CBOHelpfulData, setCBOHelpfulData] = React.useState<any>(undefined);
  const dispatch = useAppDispatch();
  const basicInformationStore = useAppSelector((store) => store.basicInformation);
  const history = useHistory();
  const formik = useFormik({
    initialValues: {
      country: '',
      is_have_us_social_security_number: true,
      social_security_number: '',
      is_graduated_california_high_school: true,
      is_three_or_more_year_graduated_california_high_school: true,
      is_currently_live_california: true,
      is_live_california_entire_life: true,
      month_live_here_since: '',
      year_live_here_since: 0,
      country_birthplace: '',
      city_birthplace: '',
      state_birthplace: '',
      number_of_year_in_us: 0,
      number_of_year_outside_us: 0,
      current_enrollment_status: '',
      when_graduate_high_school: '',
      year_plan_college_studies: 0,
      is_participate_cbo: true,
      your_cbo_array: '',
      indicate_number_community_programs: 0,
      cbo_info_array: '',
      advisor_number_community_programs: '',
      advisor_first_name: '',
      advisor_last_name: '',
      advisor_phone_number: '',
      advisor_email: '',
      is_financial_qualify_fee_waiver: true,
      indicator_economic_fee_waiver_array: '',
      fee_waiver_signature: '',
      answer_coalition_colleges_array: '',
    },
    onSubmit: async (values) => {
      const newValues = {
        ...values,
        is_have_us_social_security_number: formatBoolean(values.is_have_us_social_security_number),
        is_graduated_california_high_school: formatBoolean(
          values.is_graduated_california_high_school
        ),
        is_three_or_more_year_graduated_california_high_school: formatBoolean(
          values.is_three_or_more_year_graduated_california_high_school
        ),
        is_currently_live_california: formatBoolean(values.is_currently_live_california),
        is_live_california_entire_life: formatBoolean(values.is_live_california_entire_life),
        is_financial_qualify_fee_waiver: formatBoolean(values.is_financial_qualify_fee_waiver),
        is_participate_cbo: formatBoolean(values.is_participate_cbo),
        indicator_economic_fee_waiver_array: JSON.stringify(formFeeWeiver.values),
        answer_coalition_colleges_array: JSON.stringify(formAnswerFeeWeiver.values),
        student_id: parseInt(studentId || ''),
      };
      const responese = await dispatch(updateCitizenship(newValues));
      if (responese.payload) return;
      history.push(routers.demographics.path);
    },
  });
  const formFeeWeiver = useFormik({
    initialValues: {
      is_in_free_and_reduced_program: false,
      is_college_free: false,
      is_ACT_free: false,
      is_NACAC_free: false,
      is_Pell_Grant: false,
      is_TRIO: false,
      is_none_fee_waiver: false,
    },
    onSubmit: (values) => {},
  });
  const formAnswerFeeWeiver = useFormik({
    initialValues: {
      answer_is_in_free_and_reduced_program: false,
      answer_is_college_free: false,
      answer_is_ACT_free: false,
      answer_is_NACAC_free: false,
      answer_is_Pell_Grant: false,
      answer_is_TRIO: false,
      answer_is_none_fee_waiver: false,
    },
    onSubmit: (values) => {},
  });
  const studentId = Cookies.get('userId');
  const getDetail = async () => {
    let res = await dispatch(getCitizenship(studentId));
    const dataLoaded: any = res.payload;
    const values: any = {
      ...dataLoaded,
      is_have_us_social_security_number: BoolToString(dataLoaded.is_have_us_social_security_number),
      is_graduated_california_high_school: BoolToString(
        dataLoaded.is_graduated_california_high_school
      ),
      is_three_or_more_year_graduated_california_high_school: BoolToString(
        dataLoaded.is_three_or_more_year_graduated_california_high_school
      ),
      is_currently_live_california: BoolToString(dataLoaded.is_currently_live_california),
      is_live_california_entire_life: BoolToString(dataLoaded.is_live_california_entire_life),
      is_financial_qualify_fee_waiver: BoolToString(dataLoaded.is_financial_qualify_fee_waiver),
      is_participate_cbo: BoolToString(dataLoaded.is_participate_cbo),
    };
    formik.setValues(values);
    if (dataLoaded.indicator_economic_fee_waiver_array) {
      formFeeWeiver.setValues(JSON.parse(dataLoaded.indicator_economic_fee_waiver_array));
      formAnswerFeeWeiver.setValues(JSON.parse(dataLoaded.answer_coalition_colleges_array));
    }
    setCBOData(JSON.parse(dataLoaded.your_cbo_array));
    setCBOHelpfulData(JSON.parse(dataLoaded.cbo_info_array));
  };
  React.useEffect(() => {
    getDetail();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <div className="w-100">
      <TopProcessBar
        stepProcess={[
          { title: 'General', percentOnProcess: 30 },
          { title: 'California Residency', percentOnProcess: 40 },
          { title: 'Birthplace', percentOnProcess: 53 },
          { title: 'Enroliment Status', percentOnProcess: 62 },
          { title: 'CBO', percentOnProcess: 74 },
          { title: 'Counselor', percentOnProcess: 80 },
          { title: 'Fee Waiver', percentOnProcess: 90 },
        ]}
        title="Personal Information"
        process={1}
        icon={<PersonOutlineOutlinedIcon />}
      />

      <Container className="my-4">
        <div>
          <Form className="mx-auto" style={{ maxWidth: '1000px' }} onSubmit={formik.handleSubmit}>
            <div className="text-muted fs-3 fw-bold">Citizenship</div>
            <General formik={formik} />
            <div className="border w-100 my-3" />
            <CaliforniaResidency formik={formik} />
            <div className="border w-100 my-3" />
            <BirthPalace formik={formik} />
            <div className="border w-100 my-3" />
            <EnrolimentStatus formik={formik} />
            <div className="border w-100 my-3" />
            <CBO formik={formik} CBO={CBOData} CBOHelpful={CBOHelpfulData} />
            <div className="border w-100 my-3" />
            <Counselor formik={formik} />
            <div className="border w-100 my-3" />
            <FeeWaiver
              formik={formik}
              formFeeWeiver={formFeeWeiver}
              formAnswerFeeWeiver={formAnswerFeeWeiver}
            />
            <div className="m-3 d-flex justify-content-end border-top pt-3">
              <div className="col col-md-3">
                <Button variant="primary" type="submit" className="p-2 px-5 w-100">
                  {basicInformationStore.status === 'loading' ? (
                    <Spinner size="sm" animation="border" variant="primary" />
                  ) : (
                    'Continue'
                  )}
                </Button>
              </div>
            </div>
          </Form>
        </div>
      </Container>
    </div>
  );
};

export default Citizenship;
