import React from 'react';
import { Col, Form } from 'react-bootstrap';
import RadioInputRounded from './RadioInputRounded';

interface Props {
  values?: any;
  onChange?: any;
  name: any;
}

const YesNoSwitch = (props: Props) => {
  return (
    <Form.Group
      as={Col}
      className="my-2"
      name={props.name}
      values={props.values}
      onChange={props.onChange}
    >
      <span className="me-3">
        <RadioInputRounded
          isActive={props.values === 'true'}
          value={'true'}
          label={'Yes'}
          name={props.name}
        />
      </span>
      <span className="me-3">
        <RadioInputRounded
          isActive={props.values === 'false'}
          value={'false'}
          label={'No'}
          name={props.name}
        />
      </span>
    </Form.Group>
  );
};

export default YesNoSwitch;
