/* eslint-disable react-hooks/exhaustive-deps */
import TextIconInput from 'components/input/TextIconInput';
import YesNoSwitch from 'components/input/YesNoSwitch';
import React from 'react';
import { Card, Image, Row } from 'react-bootstrap';
import SearchIcon from '@mui/icons-material/Search';
import RemoveIcon from '@mui/icons-material/Remove';
import AddIcon from '@mui/icons-material/Add';
import SelectInput from 'components/input/SelectInput';
import CBOConfig from './components/CBOConfig';
interface Props {
  formik: any;
  CBO?: any;
  CBOHelpful?: any;
}

const CBO = (props: Props) => {
  const CBOOptions = [
    {
      id: 1,
      name: 'Cares of Washington',
      location: 'Seattle, Washington',
      iconSrc: '/assets/icons/cbo-1.png',
    },
    {
      id: 2,
      name: 'Colville Tribes',
      location: 'Nespelem, Washington',
      iconSrc: '/assets/icons/cbo-2.png',
    },
    {
      id: 3,
      name: 'Communit Action Of Skagit County',
      location: 'Mount Vernon, Washington',
      iconSrc: '/assets/icons/cbo-3.png',
    },
  ];
  const [CBO, setCBO] = React.useState<any[]>([]);
  const [CBOHelpful, setCBOHelpful] = React.useState<string[]>(['']);
  React.useEffect(() => {
    if (props.CBO && props.CBOHelpful) {
      setCBOHelpful(props.CBOHelpful);
      setCBO(props.CBO);
    }
  }, [props.CBOHelpful, props.CBO]);
  React.useEffect(() => {
    props.formik.setFieldValue('your_cbo_array', JSON.stringify(CBO));
  }, [CBO]);
  React.useEffect(() => {
    props.formik.setFieldValue('cbo_info_array', JSON.stringify(CBOHelpful));
  }, [CBOHelpful]);

  React.useEffect(() => {
    if (props.formik.values.indicate_number_community_programs === '1') {
      setCBOHelpful(['']);
    }
    if (props.formik.values.indicate_number_community_programs === '2') {
      setCBOHelpful(['', '']);
    }
    if (props.formik.values.indicate_number_community_programs === '3') {
      setCBOHelpful(['', '', '']);
    }
  }, [props.formik.values.indicate_number_community_programs]);
  return (
    <Row className="m-0">
      <div>Community-Based Organization (CBO)? </div>
      <div className="text-muted">
        Are you participating in a Community-Based Organization (CBO)?
      </div>
      <YesNoSwitch
        name="is_participate_cbo"
        values={props.formik.values.is_participate_cbo}
        onChange={props.formik.handleChange}
      />

      <Card className="border rounded m-1 py-3">
        <div>Community- Based Organizations </div>
        <div className="text-muted">You are allowed to list a maximun of 5 CBOS</div>
        <TextIconInput icon={<SearchIcon />} mdCol="12" />
        <Card className="border rounded m-1 py-3">
          {CBOOptions.map((option, index) => (
            <React.Fragment key={index}>
              <div className="d-flex align-items-end position-relative">
                <div className="text-center mx-3 me-3" style={{ width: 100 }}>
                  <Image height={34} src={option.iconSrc} />
                </div>
                <div className="me-3 pb-2">{option.name}</div>
                <div className="text-muted sized70  pb-2">{option.location}</div>
                <span className="position-absolute h-100 d-flex align-items-center end-0">
                  <AddIcon
                    fontSize="small"
                    className="bg-app text-primary rounded-circle mx-4 pointer"
                    onClick={() => {
                      if (CBO.find((item) => item.id === option.id) !== undefined) return;
                      const newCBO = [...CBO, option];
                      setCBO(newCBO);
                    }}
                  />
                </span>
              </div>
              {index + 1 < CBOOptions.length && (
                <div className="px-3">
                  <div className="w-100 border-dash my-3" />
                </div>
              )}
            </React.Fragment>
          ))}
        </Card>
        <div className="mx-2">
          Your CBOS <span className="badge bg-primary">{CBO.length ?? 0}</span>
        </div>
        {CBO.map((element, index) => (
          <Card className="border rounded m-1 py-3" key={index}>
            <div className="d-flex align-items-end position-relative">
              <div className="text-center mx-3 me-3" style={{ width: 100 }}>
                <Image height={34} src={element.iconSrc} />
              </div>
              <div className="me-3 pb-2">{element.name}</div>
              <div className="text-muted sized70  pb-2">{element.location}</div>
              <span className="position-absolute h-100 d-flex align-items-center end-0">
                <RemoveIcon
                  fontSize="small"
                  className="bg-light text-dart rounded-circle mx-4 pointer"
                  onClick={() => {
                    const newCBO = [...CBO].filter((item) => item.id !== element.id);
                    setCBO(newCBO);
                  }}
                />
              </span>
            </div>
          </Card>
        ))}
        <div className="my-1" />
        <SelectInput
          mdCol={'12'}
          option={[{ value: 1 }, { value: 2 }, { value: 3 }]}
          name="indicate_number_community_programs"
          values={props.formik.values.indicate_number_community_programs}
          onChange={props.formik.handleChange}
          label="Indicate the number of community programs or organizations that have provided you with free
        assistance in your application process.*"
        />
        {CBOHelpful.map((item, index) => (
          <CBOConfig
            key={index}
            selectedCBO={CBO}
            item={item}
            indexText={`${index + 1}/${CBOHelpful.length}`}
            handleSave={(string) => {
              const newCBOHelpful = [...CBOHelpful];
              newCBOHelpful.splice(index, 1, string);
              setCBOHelpful(newCBOHelpful);
            }}
            handleRemove={() => {
              const newCBOHelpful = [...CBOHelpful];
              newCBOHelpful.splice(index, 1);
              setCBOHelpful(newCBOHelpful);
            }}
          />
        ))}
      </Card>
    </Row>
  );
};

export default CBO;
