import * as React from 'react';
import { styled } from '@mui/material/styles';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Collapse from '@mui/material/Collapse';
import IconButton, { IconButtonProps } from '@mui/material/IconButton';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Col, Row, Button } from 'react-bootstrap';
import SelectInput from 'components/input/SelectInput';
import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircleOutline';
interface ExpandMoreProps extends IconButtonProps {
  expand: boolean;
}

const ExpandMore = styled((props: ExpandMoreProps) => {
  const { expand, ...other } = props;
  return <IconButton {...other} />;
})(({ theme, expand }) => ({
  transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
  marginLeft: 'auto',
  transition: theme.transitions.create('transform', {
    duration: theme.transitions.duration.shortest,
  }),
}));

interface Props {
  handleRemove: any;
  handleSave: (value: string) => void;
  selectedCBO: any[];
  indexText: string;
  item: string;
}

const CBOConfig = (props: Props) => {
  const [expanded, setExpanded] = React.useState(false);
  const [stringPicked, setStringPicked] = React.useState('');
  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  return (
    <Row>
      <Col xs={11}>
        <Card className="w-100 my-3">
          <CardActions
            disableSpacing
            onClick={handleExpandClick}
            sx={{
              backgroundColor: expanded ? '#F5F1FF' : 'none',
              borderBottom: expanded ? '2px solid' : 'none',
              borderBottomColor: expanded ? '#5F30E2' : 'none',
            }}
          >
            <div className="w-100 d-flex justify-content-between">
              <span className="p-1">
                Community-Based Organizations No.{props.indexText.slice(0, 1)}
              </span>
              <strong className="text-muted">{props.indexText}</strong>
            </div>
            <ExpandMore
              expand={expanded}
              onClick={handleExpandClick}
              aria-expanded={expanded}
              aria-label="show more"
            >
              <ExpandMoreIcon />
            </ExpandMore>
          </CardActions>
          <Collapse in={expanded} timeout="auto" unmountOnExit>
            <CardContent>
              <Row>
                <SelectInput
                  defautlValues={props.item}
                  option={props.selectedCBO.map((cbo) => ({ value: cbo.name }))}
                  mdCol={'12'}
                  label={'Organization from which you received the most help*'}
                  onChange={(e) => setStringPicked(e.target.value)}
                />
                <Col xs={6} md={9} />

                <Col xs={6} md={3} className="p-3">
                  <Button
                    className="w-100"
                    onClick={() => {
                      props.handleSave(stringPicked);
                      setExpanded(false);
                    }}
                  >
                    Save Info
                  </Button>
                </Col>
              </Row>
            </CardContent>
          </Collapse>
        </Card>
      </Col>
      <Col xs={1}>
        <RemoveCircleOutlineIcon
          className="text-muted pointer my-4"
          onClick={() => props.handleRemove()}
        />
      </Col>
    </Row>
  );
};

export default CBOConfig;
