import CheckboxInput from 'components/input/CheckboxInput';
import TextInput from 'components/input/textInput';
import YesNoSwitch from 'components/input/YesNoSwitch';
import React from 'react';
import { Col, Row } from 'react-bootstrap';

interface Props {
  formik: any;
  formAnswerFeeWeiver: any;
  formFeeWeiver: any;
}

const FeeWaiver = (props: Props) => {
  const economicOptions = [
    {
      key: 'is_in_free_and_reduced_program',
      value:
        'My annual family income falls within the Income Eligibility Guidelines* for the Federal Free and Reduced Lunch Program.',
    },
    { key: 'is_college_free', value: 'I have received a College Board fee waiver.' },
    { key: 'is_ACT_free', value: 'I have received an ACT fee waiver.' },
    { key: 'is_NACAC_free', value: 'I have received a NACAC fee waiver.' },
    { key: 'is_Pell_Grant', value: 'I am eligible for a Pell Grant.' },
    {
      key: 'is_TRIO',
      value: 'I have participated (or am participating) in TRIO programs (such as Upward Bound).',
    },
    { key: 'is_none_fee_waiver', value: 'None of these apply to me.' },
  ];
  return (
    <Row className="m-0">
      <div>Fee Waiver</div>
      <div className="text-muted my-1">
        Our member colleges want to make sure that application fees do not pose a barrier for any
        student who wishes to apply for admission. Do you feel that your financial circumstances
        might qualify you for an application fee waiver?*
      </div>
      <YesNoSwitch
        name="is_financial_qualify_fee_waiver"
        values={props.formik.values.is_financial_qualify_fee_waiver}
        onChange={props.formik.handleChange}
      />
      <div className="text-muted my-1">
        You must meet at least one of the following indicators of economic need to qualify for an
        application fee waiver. Select all that apply:*
      </div>
      {economicOptions.map((option) => (
        <Col md={12} key={option.key}>
          <CheckboxInput
            label={<span className="mt-2 text-muted">{option.value}</span>}
            name={option.key}
            values={props.formFeeWeiver.values[`${option.key}`]}
            onChange={props.formFeeWeiver.handleChange}
          />
        </Col>
      ))}
      <div className="text-muted my-1">
        I certify that I understand and meet the eligibility requirements to request an admission
        application fee waiver. I also understand if I am a first year student my counselor will be
        asked to verify my eligibility. Fee Waiver signature
      </div>
      <Col md={12} className="mb-1">
        <TextInput
          name="fee_waiver_signature"
          values={props.formik.values.fee_waiver_signature}
          onChange={props.formik.handleChange}
          placeholder="Your Signature"
        />
      </Col>
      <div className="text-muted my-1">
        Coalition colleges and universities may offer their own institution-specific fee waivers (or
        not charge an application fee at all). Furthermore, you may also be eligible for a fee
        waiver at participating Coalition colleges and universities based on the criteria below.
        Please answer the question below to view a list of Coalition colleges and universities where
        application fees are waived, based upon your qualification. Select all that apply
      </div>
      {economicOptions.map((option) => (
        <Col md={12} key={option.key}>
          <CheckboxInput
            label={<span className="mt-2 text-muted">{option.value}</span>}
            name={`answer_${option.key}`}
            values={props.formAnswerFeeWeiver.values[`answer_${option.key}`]}
            onChange={props.formAnswerFeeWeiver.handleChange}
          />
        </Col>
      ))}
    </Row>
  );
};

export default FeeWaiver;
