import RadioInputRounded from 'components/input/RadioInputRounded';
import SelectInput from 'components/input/SelectInput';
import TextInput from 'components/input/textInput';
import React from 'react';
import { Col, Form, Row } from 'react-bootstrap';

interface Props {
  formik: any;
}

const General = (props: Props) => {
  return (
    <Row className="m-0">
      <div className="text-muted">What is your country of citizenship?</div>
      <SelectInput
        option={[{ value: 'United State' }]}
        mdCol={'6'}
        label={'Country'}
        name={'country'}
        values={props.formik.values.country}
        onChange={props.formik.handleChange}
        feedback={
          props.formik.touched.country &&
          props.formik.errors.country && (
            <span className="text-danger">{props.formik.errors.country}</span>
          )
        }
      />
      <div className="text-muted">
        Do you have a U.S. Social Security number or an Individual Taxpayer Identification Number
        (ITIN)?
      </div>
      <div className="text-muted">
        If you apply for financial aid, we use the Social Security number or ITIN you provide to
        match your application to your Free Application for Federal Student Aid (FAFSA) or
        California Dream Act application. See 'Help' for more information on the additional uses of
        the Social Security number.
      </div>
      <Form.Group
        as={Col}
        className="my-4"
        name={'is_have_us_social_security_number'}
        values={props.formik.values.is_have_us_social_security_number}
        onChange={props.formik.handleChange}
      >
        <span className="me-3">
          <RadioInputRounded
            isActive={props.formik.values.is_have_us_social_security_number === 'true'}
            value={'true'}
            label={'Yes'}
            name={'is_have_us_social_security_number'}
          />
        </span>
        <span className="me-3">
          <RadioInputRounded
            isActive={props.formik.values.is_have_us_social_security_number === 'false'}
            value={'false'}
            label={'No'}
            name={'is_have_us_social_security_number'}
          />
        </span>
      </Form.Group>
      <Col md={6} />
      <TextInput
        mdCol={'6'}
        label={'Social Security number or ITIN'}
        name={'social_security_number'}
        values={props.formik.values.social_security_number}
        onChange={props.formik.handleChange}
        type="number"
        feedback={
          <span className="sized70">
            Sometimes required if applying for financial aid via FAFSA
          </span>
        }
      />
    </Row>
  );
};

export default General;
