import React from 'react';
import { Col, Form, Row } from 'react-bootstrap';
import RadioButton from './RadioButton';

interface Props {
  name?: string;
  values?: any;
  onChange?: any;
  options: any[];
  mdCol?: number;
}

const GroupRadioButton = (props: Props) => {
  return (
    <Form.Group
      as={Col}
      className="my-4"
      name={props.name}
      values={props.values}
      onChange={props.onChange}
    >
      <Row className="m-0">
        {(props.options || []).map((opt) => (
          <Col md={props.mdCol || 3}>
            <RadioButton
              groupValue={props.values}
              value={opt.value}
              label={opt.label}
              name={props.name || ''}
            />
          </Col>
        ))}
      </Row>
    </Form.Group>
  );
};

export default GroupRadioButton;
