export const strongPasswordRegexp = new RegExp('(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[^A-Za-z0-9])(?=.{8,})')
export const mediumPasswordRegexp = new RegExp('((?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[^A-Za-z0-9])(?=.{6,}))|((?=.*[a-z])(?=.*[A-Z])(?=.*[^A-Za-z0-9])(?=.{8,}))')

export const dateFormat = 'MM/DD/yyyy'

export const formatDate = (values: any) => {
  if (values === null) return null
  return values === ''
  ? null
  : moment(values).format(dateFormat)
}

export const formatBoolean = (values: any) => {
  if (values === 'true') return true;
  if (values === 'false') return false;
  return undefined
}
export const BoolToString = (values: boolean) => {
  if (values) {return 'true'} else return 'false';
 
}

